import Link from 'next/link';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';

export interface Activity {
  id: string;
  title: string;
  description: string;
  location: string;
  date: string;
  time: string;
  maxParticipants: number;
  currentParticipants: number;
  price: number;
  category: string;
  organizer: {
    name: string;
    age: number;
    personalityType: string;
  };
  preferredCompanion: {
    ageRange: string;
    personalityTypes: string[];
    description: string;
  };
  isMinorFriendly: boolean;
  distance?: number; // in km from user's location
}

interface ActivityCardProps {
  activity: Activity;
  userAge?: number;
  userPersonalityType?: string;
  userLocation?: string;
}

export default function ActivityCard({ 
  activity, 
  userAge, 
  userPersonalityType, 
  userLocation 
}: ActivityCardProps) {
  const spotsLeft = activity.maxParticipants - activity.currentParticipants;
  const isFullyBooked = spotsLeft <= 0;
  const isMinor = userAge && userAge < 18;
  
  // Check if user meets the activity requirements
  const meetsAgeRequirement = !activity.preferredCompanion.ageRange || 
    (userAge && checkAgeRange(userAge, activity.preferredCompanion.ageRange));
  
  const meetsPersonalityRequirement = !userPersonalityType || 
    activity.preferredCompanion.personalityTypes.length === 0 ||
    activity.preferredCompanion.personalityTypes.includes(userPersonalityType);

  // For minors, check distance and age restrictions
  const meetsMinorRestrictions = !isMinor || (
    activity.isMinorFriendly && 
    (!activity.distance || activity.distance <= 30) &&
    activity.organizer.age <= 17
  );

  const canJoin = meetsAgeRequirement && meetsPersonalityRequirement && meetsMinorRestrictions && !isFullyBooked;

  return (
    <Card className="h-full flex flex-col hover:shadow-lg transition-shadow">
      <CardHeader>
        <div className="flex justify-between items-start">
          <div className="flex-1">
            <CardTitle className="text-lg mb-2">{activity.title}</CardTitle>
            <CardDescription className="line-clamp-2">
              {activity.description}
            </CardDescription>
          </div>
          <Badge variant={activity.isMinorFriendly ? "secondary" : "outline"}>
            {activity.category}
          </Badge>
        </div>
      </CardHeader>
      
      <CardContent className="flex-1 flex flex-col">
        <div className="space-y-3 flex-1">
          {/* Location and Date */}
          <div className="space-y-1">
            <p className="text-sm font-medium">📍 {activity.location}</p>
            {activity.distance && (
              <p className="text-xs text-muted-foreground">
                ~{activity.distance}km de tu ubicación
              </p>
            )}
            <p className="text-sm text-muted-foreground">
              🗓️ {activity.date} a las {activity.time}
            </p>
          </div>

          {/* Organizer Info */}
          <div className="p-3 bg-muted rounded-lg">
            <p className="text-sm font-medium">Organizador:</p>
            <p className="text-sm text-muted-foreground">
              {activity.organizer.name}, {activity.organizer.age} años
            </p>
            <Badge variant="outline" className="text-xs mt-1">
              {activity.organizer.personalityType}
            </Badge>
          </div>

          {/* Preferred Companion */}
          <div className="space-y-2">
            <p className="text-sm font-medium">Busca compañía:</p>
            <p className="text-xs text-muted-foreground">
              {activity.preferredCompanion.description}
            </p>
            <div className="flex flex-wrap gap-1">
              {activity.preferredCompanion.personalityTypes.map((type, index) => (
                <Badge 
                  key={index} 
                  variant={userPersonalityType === type ? "default" : "secondary"}
                  className="text-xs"
                >
                  {type}
                </Badge>
              ))}
            </div>
          </div>

          {/* Participants and Price */}
          <div className="flex justify-between items-center text-sm">
            <span className="text-muted-foreground">
              {activity.currentParticipants}/{activity.maxParticipants} personas
            </span>
            <span className="font-semibold">
              ${activity.price.toLocaleString()}
            </span>
          </div>

          {/* Restrictions for minors */}
          {isMinor && !meetsMinorRestrictions && (
            <div className="p-2 bg-destructive/10 border border-destructive/20 rounded text-xs text-destructive">
              ⚠️ Esta actividad no cumple con las restricciones para menores
            </div>
          )}

          {/* Compatibility indicators */}
          {!canJoin && !isFullyBooked && (
            <div className="space-y-1">
              {!meetsAgeRequirement && (
                <p className="text-xs text-muted-foreground">
                  ⚠️ No cumples con el rango de edad preferido
                </p>
              )}
              {!meetsPersonalityRequirement && (
                <p className="text-xs text-muted-foreground">
                  ⚠️ Tu personalidad no coincide con las preferencias
                </p>
              )}
            </div>
          )}
        </div>

        {/* Action Buttons */}
        <div className="flex gap-2 mt-4">
          <Link href={`/activities/${activity.id}`} className="flex-1">
            <Button variant="outline" className="w-full">
              Ver Detalles
            </Button>
          </Link>
          
          {isFullyBooked ? (
            <Button disabled className="flex-1">
              Completo
            </Button>
          ) : canJoin ? (
            <Link href={`/activities/${activity.id}/join`} className="flex-1">
              <Button className="w-full">
                Unirse (${activity.price.toLocaleString()})
              </Button>
            </Link>
          ) : (
            <Button disabled className="flex-1">
              No Compatible
            </Button>
          )}
        </div>
      </CardContent>
    </Card>
  );
}

// Helper function to check if user age falls within the preferred range
function checkAgeRange(userAge: number, ageRange: string): boolean {
  // Parse age ranges like "18-25", "25+", "18-30"
  if (ageRange.includes('+')) {
    const minAge = parseInt(ageRange.replace('+', ''));
    return userAge >= minAge;
  }
  
  if (ageRange.includes('-')) {
    const [min, max] = ageRange.split('-').map(age => parseInt(age));
    return userAge >= min && userAge <= max;
  }
  
  // Exact age match
  return userAge === parseInt(ageRange);
}
