export default function ActivitiesPage() {
  const [activities, setActivities] = useState<Activity[]>(sampleActivities);
  const [filteredActivities, setFilteredActivities] = useState<Activity[]>(sampleActivities);
  const [searchTerm, setSearchTerm] = useState('');
  const [categoryFilter, setCategoryFilter] = useState<string>('all');
  const [personalityFilter, setPersonalityFilter] = useState<string>('all');
  const [minorFriendlyFilter, setMinorFriendlyFilter] = useState<string>('all');

  // Mock user data (in a real app, this would come from authentication/context)
  const mockUser = {
    age: 25,
    personalityType: 'social',
    location: 'Bogotá, Colombia',
    isMinor: false
  };

  // Get unique categories and personality types for filters
  const categories = Array.from(new Set(activities.map(a => a.category)));
  const personalityTypes = Array.from(new Set(
    activities.flatMap(a => a.preferredCompanion.personalityTypes)
  ));

  // Filter activities based on search and filters
  useEffect(() => {
    let filtered = activities;

    // Search filter
    if (searchTerm) {
      filtered = filtered.filter(activity =>
        activity.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        activity.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
        activity.location.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    // Category filter
    if (categoryFilter !== 'all') {
      filtered = filtered.filter(activity => activity.category === categoryFilter);
    }

    // Personality filter
    if (personalityFilter !== 'all') {
      filtered = filtered.filter(activity =>
        activity.preferredCompanion.personalityTypes.includes(personalityFilter)
      );
    }

    // Minor-friendly filter
    if (minorFriendlyFilter !== 'all') {
      const isMinorFriendly = minorFriendlyFilter === 'true';
      filtered = filtered.filter(activity => activity.isMinorFriendly === isMinorFriendly);
    }

    setFilteredActivities(filtered);
  }, [searchTerm, categoryFilter, personalityFilter, minorFriendlyFilter, activities]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-background to-muted">
      {/* Header */}
      <header className="border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
        <div className="container mx-auto px-4 py-4 flex justify-between items-center">
          <Link href="/" className="text-2xl font-bold">
            PlanConnect
          </Link>
          <div className="space-x-4">
            <Link href="/activities/create">
              <Button>Crear Actividad</Button>
            </Link>
            <Link href="/profile">
              <Button variant="ghost">Mi Perfil</Button>
            </Link>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="container mx-auto px-4 py-8">
        {/* Page Header */}
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold mb-4">
            Descubre Actividades Increíbles
          </h1>
          <p className="text-xl text-muted-foreground max-w-2xl mx-auto">
            Encuentra la actividad perfecta para ti y conecta con personas que comparten tus intereses
          </p>
        </div>

        {/* User Info Card */}
        <Card className="mb-8">
          <CardHeader>
            <CardTitle>Tu Perfil</CardTitle>
            <CardDescription>
              Las actividades se muestran basadas en tu personalidad y preferencias
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="flex flex-wrap gap-4 items-center">
              <Badge variant="outline">
                {mockUser.age} años
              </Badge>
              <Badge>
                Personalidad: {mockUser.personalityType}
              </Badge>
              <Badge variant="secondary">
                📍 {mockUser.location}
              </Badge>
              {mockUser.isMinor && (
                <Badge variant="destructive">
                  Menor de edad - Restricciones aplicadas
                </Badge>
              )}
            </div>
          </CardContent>
        </Card>

        {/* Filters */}
        <Card className="mb-8">
          <CardHeader>
            <CardTitle>Filtros</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
              <div>
                <label className="text-sm font-medium mb-2 block">Buscar</label>
                <Input
                  placeholder="Buscar actividades..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
              </div>
              
              <div>
                <label className="text-sm font-medium mb-2 block">Categoría</label>
                <Select value={categoryFilter} onValueChange={setCategoryFilter}>
                  <SelectTrigger>
                    <SelectValue placeholder="Todas las categorías" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Todas las categorías</SelectItem>
                    {categories.map(category => (
                      <SelectItem key={category} value={category}>
                        {category}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div>
                <label className="text-sm font-medium mb-2 block">Personalidad</label>
                <Select value={personalityFilter} onValueChange={setPersonalityFilter}>
                  <SelectTrigger>
                    <SelectValue placeholder="Todas las personalidades" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Todas las personalidades</SelectItem>
                    {personalityTypes.map(type => (
                      <SelectItem key={type} value={type}>
                        {type}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div>
                <label className="text-sm font-medium mb-2 block">Edad</label>
                <Select value={minorFriendlyFilter} onValueChange={setMinorFriendlyFilter}>
                  <SelectTrigger>
                    <SelectValue placeholder="Todas las edades" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Todas las edades</SelectItem>
                    <SelectItem value="true">Apto para menores</SelectItem>
                    <SelectItem value="false">Solo adultos</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Activities Grid */}
        <div className="mb-6">
          <h2 className="text-2xl font-semibold mb-4">
            Actividades Disponibles ({filteredActivities.length})
          </h2>
          
          {filteredActivities.length === 0 ? (
            <Card>
              <CardContent className="text-center py-12">
                <p className="text-muted-foreground mb-4">
                  No se encontraron actividades que coincidan con tus filtros
                </p>
                <Button onClick={() => {
                  setSearchTerm('');
                  setCategoryFilter('all');
                  setPersonalityFilter('all');
                  setMinorFriendlyFilter('all');
                }}>
                  Limpiar Filtros
                </Button>
              </CardContent>
            </Card>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {filteredActivities.map(activity => (
                <ActivityCard
                  key={activity.id}
                  activity={activity}
                  userAge={mockUser.age}
                  userPersonalityType={mockUser.personalityType}
                  userLocation={mockUser.location}
                />
              ))}
            </div>
          )}
        </div>

        {/* CTA Section */}
        <Card className="text-center">
          <CardContent className="py-12">
            <h3 className="text-2xl font-bold mb-4">
              ¿No encuentras lo que buscas?
            </h3>
            <p className="text-muted-foreground mb-6">
              Crea tu propia actividad y encuentra a las personas perfectas para acompañarte
            </p>
            <Link href="/activities/create">
              <Button size="lg">
                Crear Nueva Actividad
              </Button>
            </Link>
          </CardContent>
        </Card>
      </main>
    </div>
  );
}
